body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes imagesmove {
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}

@keyframes imagesmove {
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}

.images-svg {
  height: 8vmax;
  width: auto;
  -webkit-animation: imagesmove 5s infinite;
          animation: imagesmove 5s infinite;
}

.images-svg:hover {
  -webkit-filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg)
    brightness(104%) contrast(97%);
          filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg)
    brightness(104%) contrast(97%);
}

.images-header {
  max-height: 25vh;
  height: 110px;
  display: flex;
  background-color: #c0c0c0;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
}

.App-header {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

ol,
ul {
  padding-left: 30px;
}

.status {
  font-size: 4rem;
  font-weight: bold;
}

.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #c0c0c0;
  height: 100vh;
  font-family: "Ubuntu", sans-serif;
  min-width: 900px;
}

.game-info {
  display: flex;
  width: 25%;
  flex-direction: column;
}

.game-options {
  display: flex;
  width: 25%;
  flex-direction: column;
  margin: 5px 0px 0px 0px;
}

.game-board {
  width: 50%;
  text-align: center;
}

.board-row-div {
  justify-content: center;
  float: left;
  position: relative;
  left: 50%;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.square {
  background: #ffffff;
  border: 1px solid #999;
  float: left;
  position: relative;
  left: -50%;
  font-size: 4rem;
  font-weight: 900;
  line-height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  height: 120px;
  width: 120px;
}

.square:focus {
  outline: none;
  background: #ddd;
}

.btn-secondary {
  margin: 5px;
  width: 180px;
  outline: none !important;
}

.btn-success {
  margin: 10px 0px 10px 35px;
  outline: none !important;
  width: 180px;
}

.btn-dark {
  margin: 5px;
  width: 180px;
  outline: none !important;
  float: right;
}

.square--winning {
  background-color: yellow !important;
}
select {
  background-color: #343a40 !important;
  color: #fff !important;
}

.form-control {
  width: 180px !important;
  float: right;
  margin: 5px;
  outline: none !important;
}

#container {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  font-family: "Ubuntu", sans-serif;
  background-color: #c0c0c0;
}

#left {
  display: flex;
  flex-direction: column;
  width: 50%;
  background-color: whitesmoke;
  border: solid gray 3px;
  height: 100vh;
  border-radius: 15px;
  margin-top: 20px;
}

#leftChild {
  width: 100%;
  background-color: whitesmoke;
  text-align: center;
  border: 0px;
  height: 100%;
  overflow: scroll;
  border-radius: 15px;
}

#right {
  width: 35%;
  background-color: whitesmoke;
  border: solid gray 3px;
  height: 100vh;
  border-radius: 15px;
  color: gray;
  margin-top: 20px;
}

.card-div {
  box-sizing: border-box;
  display: inline-block;
  margin: auto;
  padding: 8px;
  background-color: burlywood;
  min-height: 180px;
  text-align: center;
  align-self: center;
  margin: 0px 0 20px 0;
  width: 90%;
  border: solid gray 2px;
  border-radius: 15px;
}

.card-div button {
  align-content: center;
  margin: 5px;
  outline: none;
  border: solid gray 1px;
  color: white;
  border-radius: 5px;
  background-color: gray;
}

.card-div .card-input {
  align-content: center;
  display: inline-block;
}

.card-div:nth-child(odd) {
  background-color: lightgoldenrodyellow;
}

.page-title,
h2 {
  text-align: center;
  color: grey;
}
#right-btns {
  border-radius: 5px;
  width: auto;
  align-self: center;
  color: gray;
}

#addAccBtn {
  border: solid gray 1px;
  outline: none;
  margin: 5px 0px 5px 0px;
  border-radius: 5px;
  width: 200px;
  align-self: center;
  color: gray;
}

.right-input-label {
  float: left;
  clear: left;
  width: 150px;
  text-align: left;
}

.right-input {
  float: left;
  width: 200px;
}

form {
  margin: 10px;
}

.para-left-card-name,
.para-left-card-output {
  margin: 0px;
}

.results-label {
  float: left;
  clear: left;
  width: 150px;
  text-align: left;
}

.results-span {
  float: left;
  width: 200px;
}

